import React, { useState } from 'react';
import { useDoubleTap } from 'use-double-tap';
import { useLongPress } from 'use-long-press';
import serviceFactory from '../utils/service-factory';

function DeviceButton(props) {
  const { deviceInfo } = props;
  const [editMode, setEditMode] = useState('');
  const [deviceName, setDeviceName] = useState(deviceInfo.name);

  const exitEditMode = () => {
    // Service call to update name, roomId, and maybe homeId of device
    setEditMode('');
  };

  const doubleClick = useDoubleTap(
    (event) => {
      event.stopPropagation();
      if (!editMode) {
        return (deviceInfo.deviceType === 2
          ? serviceFactory.toggleLightFromWebhook(deviceInfo.webHookURL.replace('{STATUS}', 'off')) : null);
      }
      exitEditMode();
    },
    300,
    {
      onSingleTap: (event) => {
        event.stopPropagation();
        if (!editMode) {
          return (deviceInfo.deviceType === 2
            ? serviceFactory.toggleLightFromWebhook(deviceInfo.webHookURL.replace('{STATUS}', 'on')) : null);
        }
      },
    },
  );

  const longClick = useLongPress(
    (event) => {
      event.stopPropagation();
      console.log('LONG');
      setEditMode('draggable');
      document.getElementById(`name-${deviceInfo.id}`).focus();
    }, {
    },
  );

  const onNameChange = (event) => {
    setDeviceName(event.target.value.replace(/\n/g, ''));
  };

  document.addEventListener('click', (e) => {
    if (!document.getElementById(`button-${deviceInfo.id}`).contains(e.target)) {
      exitEditMode();
    }
  });

  return (
    <div
      id={`button-${deviceInfo.id}`}
      className={`device ${editMode}`}
      draggable={!!editMode}
      {...longClick()}
      {...doubleClick}
    >
      <textarea
        type="text"
        value={deviceName}
        onChange={onNameChange}
        id={`name-${deviceInfo.id}`}
        readOnly={!editMode}
        style={{ pointerEvents: editMode ? 'auto' : 'none' }}
      />
      {console.log(deviceInfo)}
    </div>
  );
}

export default DeviceButton;
