const constructDevicesLists = (listQueryResults) => {
  const listHash = {};
  console.log(listQueryResults);
  if (listQueryResults.devices) { // Home and Room
    listQueryResults.devices.forEach(row => {
      if (listHash[row[6]] && listHash[row[6]].rooms.some(room => room.id === row[4])) {
        console.log(11111, row[5]);
        listHash[row[6]].rooms.filter(room => room.id === row[4])[0].devices.push(row[0] ? {
          id: row[0],
          name: row[1],
          deviceType: row[2],
          webHookURL: row[3],
        } : {});
      } else if (listHash[row[6]]) { // Home no room
        console.log(222222, row[5]);
        console.log(listHash[row[6]].rooms.some(room => room.id === row[4]));
        console.log(row[4]);
        listHash[row[6]].rooms.push({
          id: row[4],
          name: row[5],
          devices: row[0] ? [{
            id: row[0],
            name: row[1],
            deviceType: row[2],
            webHookURL: row[3],
          }] : [],
        });
        console.log(222222, listHash);
      } else { // No Matching Home or Room
        console.log(33333, row[5]);
        listHash[row[6]] = {
          homeId: row[6],
          key: row[6],
          homeName: row[7],
          value: row[7],
          rooms: row[4] ? [{
            id: row[4],
            name: row[5],
            devices: [{
              id: row[0],
              name: row[1],
              deviceType: row[2],
              webHookURL: row[3],
            }],
          }] : [],
        };
      }
    });
  }

  const constructedListTasks = Object.keys(listHash)
    .map((key) => listHash[key]);
  return constructedListTasks;
};

export default constructDevicesLists;
