
import React, { useState, useEffect } from 'react';
import getConstants from '../utils/constants';
import serviceFactory from '../utils/service-factory';
import constructDevicesLists from '../utils/constructDevicesLists';
import MockDevicesData from '../utils/MockDevicesData';
import Screen from './homeManagerScreen';
import './homeManager.scss';

function HomeManager() {
  const CONSTANTS = getConstants();
  const [rawDevices, setRawDevices] = useState(null);
  const [reconstructTrigger, setReconstructTrigger] = useState(false);
  const [constructedDevicesLists, setConstructedDevicesLists] = useState(null);
  const [defaultHome, setDefaultHome] = useState(undefined);
  const [userToken, setUserToken] = useState(
    localStorage.getItem('userToken') === 'undefined' || localStorage.getItem('userToken') == null
      ? undefined
      : localStorage.getItem('userToken').split(','),
  );

  useEffect(() => {
    if (reconstructTrigger) {
      setReconstructTrigger(false);
      setRawDevices(null);
      setConstructedDevicesLists(null);
    }
  }, [reconstructTrigger]);

  useEffect(() => {
    if (!rawDevices && !constructedDevicesLists && userToken) {
      console.log(123);
      // setRawDevices(MockDevicesData);
      serviceFactory.getDevicesByRoom(userToken[0], setRawDevices);
      // serviceFactory.getRemindersRequest(userToken[0], setRawDevices);
    } else if (rawDevices && !constructedDevicesLists && userToken) {
      console.log(234);
      const devicesLists = constructDevicesLists(rawDevices);
      setConstructedDevicesLists(devicesLists);
      setDefaultHome(devicesLists[0]);
    }
  }, [rawDevices, constructedDevicesLists, userToken]);

  return (
    ((rawDevices && constructedDevicesLists) || !userToken)
    && (
      <div className="default-margin">
        {/* <Screen
          devicesList={constructedDevicesLists}
          triggerListsReconstruction={() => setReconstructTrigger(true)}
          userToken={userToken}
          setUserToken={setUserToken}
          unloaded={!rawDevices || !constructedDevicesLists}
          remindersScreenActive={currentActivePage === 'reminders'}
        /> */}
        <Screen
          devicesList={constructedDevicesLists}
          triggerListsReconstruction={() => setReconstructTrigger(true)}
          userToken={userToken}
          setUserToken={setUserToken}
          defaultHome={defaultHome}
        />
      </div>
    )
  );
}

export default HomeManager;
