import React, { useState } from 'react';
import { useDoubleTap } from 'use-double-tap';
import Selector from '../components/dropdownSelector';
import serviceFactory from '../utils/service-factory';
import DeviceButton from './deviceButton';

function HomeDevicesView(props) {
  const { homes, defaultHome } = props;
  const [activeHome, setActiveHome] = useState(defaultHome);

  const selectHome = (selectedHome) => {
    console.log(homes);
    setActiveHome(homes?.filter(home => home.key === selectedHome.key)[0]);
  };

  console.log(homes);

  return (
    <div className="manager">
      {
        homes
        && (
          <Selector
            options={homes}
            selectedOption={activeHome}
            onChange={selectHome}
          />
        )
      }
      <div className="manager-body">
        {console.log(activeHome)}
        {activeHome?.rooms && activeHome.rooms.length > 0 ? activeHome.rooms.map(r => (
          <div className="room">
            <h2 className="room-header">
              {r.name}
            </h2>
            <div className="devices">
              {
                r.devices && r.devices.length > 0
                  ? r.devices.map(d => (<DeviceButton deviceInfo={d} />)) : <h3>No Devices In Room</h3>
              }
            </div>
          </div>
        )) : <h2>No Rooms In Home</h2>}
      </div>
    </div>
  );
}

export default HomeDevicesView;
